<template>
  <div>
    <p>Latitude: {{ latitude }}</p>
    <p>Longitude: {{ longitude }}</p>
    <br />
    <div>
      <select
        v-model="selectedOrganization"
        @change="onOrganizationChange"
      >
        <option
          v-for="org in organizations"
          :value="org"
          :key="org.id"
        >
          {{ org.nameOfOrganization }}
        </option>
      </select>
    </div>
    <span>
      <p>{{ userMessage }}</p>
    </span>
    <button @click="getLocation">Get location</button>

    <p>
      Distance to Organization: {{ distanceToOrganization.toFixed(2) }} meters
    </p>
  </div>
</template>

<script setup>
import { ref } from "vue";
import { point, distance } from "@turf/turf";

const latitude = ref(null);
const longitude = ref(null);
const userMessage = ref("");
const distanceToOrganization = ref(0);

const organizations = ref([
  {
    id: 1,
    nameOfOrganization: "Convenience, Dingli",
    latitude: 35.861579718553045,
    longitude: 14.384102888618935,
    meters: 20,
    startTime: "2024-02-08T11:00:00Z",
    endTime: "2024-02-08T11:03:00Z",
  },
  {
    id: 2,
    nameOfOrganization: "Barbajean",
    latitude: 35.86092166581468,
    longitude: 14.382550960199088,
    meters: 20,
    startTime: "2024-02-08T11:00:00Z",
    endTime: "2024-02-08T11:03:00Z",
  },
  {
    id: 3,
    nameOfOrganization: "Patrick Prints",
    latitude: 35.87057691018765,
    longitude: 14.446694085537114,
    meters: 20,
    startTime: "2024-02-08T11:00:00Z",
    endTime: "2024-04-08T11:03:00Z",
  },
  {
    id: 4,
    nameOfOrganization: "Master Grocer",
    latitude: 35.87154161786839,
    longitude: 14.446766513597746,
    meters: 20,
    startTime: "2024-02-08T11:00:00Z",
    endTime: "2024-04-08T11:03:00Z",
  },
]);

const selectedOrganization = ref(organizations.value[0]);

const getLocation = () => {
  const url =
    "https://www.googleapis.com/geolocation/v1/geolocate?key=AIzaSyAizlo3Mfwncv2biC86x_U4-B7FV2V2qFg";
  fetch(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
  })
    .then((response) => response.json())
    .then((data) => {
      latitude.value = data.location.lat;
      longitude.value = data.location.lng;
    })
    .catch((error) => {
      console.error("Error fetching location:", error);
    });
};

const onOrganizationChange = () => {
  if (latitude.value !== null && longitude.value !== null) {
    const from = point([longitude.value, latitude.value]);
    const to = point([
      selectedOrganization.value.longitude,
      selectedOrganization.value.latitude,
    ]);
    distanceToOrganization.value = distance(from, to, { units: "meters" });
  }
};
</script>
